<template>
    <h1>Hello World</h1>
    <input type="text" v-model="inputText" placeholder="할 일 입력"/>
    <button v-on:click="addTodo" >Add</button>
    <ul>
        <li v-for="(todo,index) in todos" :key="index" id="list">
            <span>
                {{ todo.text }}
                <button v-on:click="removeTodo(index)" class="btn-outline-danger">Remove</button>
            </span>
        </li>
    </ul>
</template>
<script>
export default{
    data(){
        return {
            inputText: "s",
            todos: []
        }
    },
    methods:{
        addTodo(){
            this.todos.push({text:this.inputText});
            this.inputText="";  
        },
        removeTodo(index){
            this.todos.splice(index, 1);
        }
    }
}
</script>
<style>
li{

}
span{
display: flex;
justify-content: space-between;
}
</style>